
html,
body {
  background-color: white;
}
.bg {
  position: fixed;
  height: 100%;
  width: 100%;
}
.vux-demo {
  text-align: center;
}
.logo {
  width: 100px;
  height: 100px;
}
.form-main {
  position: absolute;
  height: 100%;
  width: 100%;
}
.form-input {
  position: absolute;
  height: auto;
  font-size: small;
  top: 10%;
  width: 75%;
  left: 12.5%;
}
.form-input /deep/ .vux-check-icon > .weui-icon-success:before,
.vux-check-icon > .weui-icon-success-circle:before {
  color: #316edb;
}



